import React from 'react';
import '../assets/css/Datainput.css';
import { NavLink } from 'react-router-dom';
import Goback from './Goback';

function Datainput() {
    
    return (
        <div className="datainput">
        
            <h3>Carga de datos</h3>
            <div className="row options">
            
                <div className="col-6">
                <NavLink to="/departments">
                    <div className="input-button">
                    <h6>Departamentos</h6>
                    </div>
                </NavLink>
                </div>
                
                <div className="col-6">
                    <NavLink to="/services">
                    <div className="input-button">
                    <h6>Servicios</h6>
                    </div>
                    </NavLink>
                </div>
                
            </div>
            <div className="row options">
                <div className="col-6">
                    <NavLink to="/guard">
                    <div className="input-button">
                        <h6>Guardias, Turnos...</h6>
                    </div>
                    </NavLink>
                </div>
                <div className="col-6">
                    <NavLink to="/personal">
                    <div className="input-button">
                        <h6>Personal</h6>
                    </div>
                    </NavLink>
                </div>
            </div>
            <div className="row options">
                <div className="col-6">
                    <div className="input-button">
                        <h6>Licencias</h6>
                    </div>
                </div>
                <div className="col-6">
                    <div className="input-button">
                        <h6>Feriados</h6>
                    </div>
                </div>
            </div>
            <Goback />
        </div>

       
    )
}

export default Datainput;